import React, { Component } from "react";
import Navigation from "../../components/Navigation/Navigation";
import Accounts from "../../components/Accounts/Accounts";
import TransactionTable from "../../components/Table/Table";
import Footer from "../../components/Footer/Footer";
import config from "../../config";
import axios from "axios";

import "./Dashboard.css";

class Dashboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      accounts: {},
      loading: true,
      balances: {}
    };
  }
  componentDidMount = () => {
    let clientId = localStorage.getItem("clientId");
    if (clientId) {
      this.loadAccounts(localStorage.getItem("clientId"));
    } else {
      this.setState({
        notification: "unable to load client id"
      });
    }
  };

  loadAccounts = clientId => {
    let that = this;
    axios({
      method: "get",
      url: config.ACCOUNT_URL + clientId,
      responseType: "json"
    })
      .then(resp => {
        that.state.accounts = resp.data;
        that.loadBalances(resp.data);
      })
      .catch(e => {
        console.log("exception while loading accounts ", e);
      });
  };

  loadBalances = data => {
    let that = this;
    let accountObj = {};
    data.map((account, index) => {
      return axios({
        method: "get",
        url: config.BALANCE_URL + account.accountId,
        responseType: "json"
      })
        .then(resp => {
          accountObj[account.accountId] = resp.data;
          if (data.length - 1 === index) {
            that.state.balances = accountObj;
            setTimeout(() => {
              this.setState({
                loading: false
              });
            }, 1000);
          }
        })
        .catch(e => {
          console.log("exception while loading balances ", e);
        });
    });
  };

  loadAccountDetails(id) {
    console.log("LOAD DETAILS", id);
  }

  loadAccountComponents() {
    if (this.state.loading === true) return <div>Loading...</div>;
    return (
      <div>
        {this.state.accounts.map((account, index) => {
          return (
            <Accounts
              loadAccountDetails={this.loadAccountDetails}
              account={account}
              balance={this.state.balances[account.accountId]}
              key={index}
            />
          );
        })}
      </div>
    );
  }

  render() {
    return (
      <div>
        <Navigation />
        <div className="container liquify-dashboard">
          <div className="row">
            <div className="col-sm-5 left-block">
              <h2 className="section-title">My accounts</h2>
              {this.loadAccountComponents()}
            </div>
            <div className="col-sm-7 right-block">
              <h2 className="section-title">Treasury transactions</h2>
              <TransactionTable />

              <button
                type="button"
                className="btn btn-secondary btn-sm btn-block transaction-btn"
              >
                Load more transactions
              </button>
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}
export default Dashboard;
