import React from "react";
import symbols from "../../currencySymbols";
import "./Accounts.css";

const getCurrencySymbol = currency => {
  if (symbols[currency])
    return <span dangerouslySetInnerHTML={{ __html: symbols[currency] }} />;
  else return currency;
};

const Accounts = props => {
  return (
    <div
      className="card account-card float-left"
      onClick={() => {
        props.loadAccountDetails(props.account.accountId);
      }}
    >
      <div className="card-body">
        <div className="card-text">
          <div>
            <span className={"flag " + props.account.currency} />
            <span className="currency"> {props.account.currency}</span>
          </div>
          <div className="clearfix" />
          <div>
            <span className="amount">
              {props.account.currency &&
                getCurrencySymbol(props.account.currency)}{" "}
              {props.balance.cashBalance.toLocaleString()}
            </span>
          </div>
          <hr />
          <div>
            <span className="account-link">
              <span className="account-type">{props.account.accountType}</span>{" "}
              {props.account.counterParty} {props.account.accountId}
            </span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Accounts;
