import React from "react";
import "./Checkbox.css";

const Checkbox = props => {
  return (
    <div className="checkbox-container">
      <input
        onChange={e => {
          props.checkboxEvent(e);
        }}
        type="checkbox"
        className="form-check-input"
        id="rememberme"
      />
      <label className="form-check-label" htmlFor="rememberme">
        {props.textLable}
      </label>
    </div>
  );
};

export default Checkbox;
