import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import Checkbox from "../components/Checkbox/Checkbox";
import Spinner from "../components/Spinner/Spinner";
import axios from "axios";
import config from "../config";

import "./Login.css";
import loginImage from "../assets/images/login-image.png";

class Login extends Component {
  constructor(props) {
    super(props);
    this.username = React.createRef();
    this.state = {
      loading: false,
      loginbtn: "btn login-btn login-button",
      labelClassUsername:
        "login-form-control-lable login-form-control-lable-hidden",
      labelClassPassword:
        "login-form-control-lable login-form-control-lable-hidden"
    };
  }

  setFormData(e) {
    this.setState(
      {
        [e.target.name]: e.target.value
      },
      () => {
        if (
          this.state.username &&
          this.state.username !== "" &&
          (this.state.password && this.state.password !== "")
        ) {
          this.setState({
            loginbtn: "btn login-btn login-button-active"
          });
        } else {
          this.setState({
            loginbtn: "btn login-btn login-button"
          });
        }
      }
    );
  }

  loginSubmit = () => {
    if (this.state.username === "" && this.state.password === "") {
      return false;
    } else {
      this.setState({
        userInputStyle: "",
        pwdInputStyle: "",
        passwordError: "",
        usernameError: "",
        loading: true
      });
      axios({
        method: "get",
        url: config.LOGIN_URL,
        headers: {
          username: this.state.username,
          password: this.state.password
        },
        responseType: "json"
      })
        .then(resp => {
          console.log(resp);
          sessionStorage.setItem("clientId", resp.data.username);
          this.setState({ redirectToReferrer: true, loading: false });
        })
        .catch(err => {
          console.log("ERROR ^^", err.response.data);
          if (err.response.data.message === "401 Invalid Password") {
            this.setState({
              passwordError: "Invalid password",
              pwdInputStyle: "login-form-input-err",
              loading: false
            });
          } else if (err.response.data.message === "401 User Not Found") {
            this.setState({
              usernameError: "Invalid user",
              userInputStyle: "login-form-input-err",
              loading: false
            });
          } else {
            this.setState({
              notification: "SOME ERROR OCCORED",
              loading: false
            });
          }
        });
    }
  };
  checkboxEvent(e) {
    console.log("check box ", e.target.checked);
  }
  setFocusStyle(e) {
    if (e.target.name === "username") {
      this.setState({
        labelClassUsername:
          "login-form-control-lable login-form-control-lable-active"
      });
    } else if (e.target.name === "password") {
      this.setState({
        labelClassPassword:
          "login-form-control-lable login-form-control-lable-active"
      });
    }
  }
  setBlurStyle(e) {
    if (e.target.name === "username" && e.target.value === "") {
      this.setState({
        labelClassUsername:
          "login-form-control-lable login-form-control-lable-hidden"
      });
    } else if (e.target.name === "password" && e.target.value === "") {
      this.setState({
        labelClassPassword:
          "login-form-control-lable login-form-control-lable-hidden"
      });
    }
  }
  render() {
    const redirectToReferrer = this.state.redirectToReferrer;
    if (redirectToReferrer === true) {
      return <Redirect to="/dashboard" />;
    }
    return (
      <div className="row login-wrapper">
        {this.state.loading && <Spinner />}
        <div className="col-sm login-left-col">
          <span className="helper" />
          <img src={loginImage} alt="login_image" />
        </div>
        <div className="col-sm login-right-col">
          <div className="logo" />
          <span className="logo-title">automate your treasuary transfer</span>
          <form className="login-form">
            <h5 className="login-form-heading">Login</h5>
            <div className="form-group login-form-control-wrapper">
              <label
                htmlFor="username"
                className={this.state.labelClassUsername}
              >
                username
              </label>
              <input
                autoComplete="off"
                type="text"
                className={
                  "form-control login-form-input " + this.state.userInputStyle
                }
                name="username"
                ref="username"
                required
                aria-describedby="username"
                onChange={e => {
                  this.setFormData(e);
                }}
                onFocus={e => {
                  this.setFocusStyle(e);
                }}
                onBlur={e => {
                  this.setBlurStyle(e);
                }}
              />
              {this.state.usernameError && (
                <span className="login-form-text-err">
                  {this.state.usernameError}
                </span>
              )}
            </div>
            <div className="form-group login-form-control-wrapper">
              <label
                htmlFor="password"
                className={this.state.labelClassPassword}
              >
                password
              </label>
              <input
                type="password"
                ref="password"
                required
                name="password"
                className={
                  "form-control login-form-input " + this.state.pwdInputStyle
                }
                id="password"
                onChange={e => {
                  this.setFormData(e);
                }}
                onFocus={e => {
                  this.setFocusStyle(e);
                }}
                onBlur={e => {
                  this.setBlurStyle(e);
                }}
              />
              {this.state.passwordError && (
                <span className="login-form-text-err">
                  {this.state.passwordError}
                </span>
              )}
            </div>
            <div className="form-group form-check">
              <Checkbox
                checkboxEvent={this.checkboxEvent}
                textLable="Remember me"
              />
            </div>
            <div className="row">
              <div className="col-sm-5 col-form-label">
                <button
                  type="button"
                  onClick={this.loginSubmit.bind(this)}
                  className={this.state.loginbtn}
                >
                  Sign in
                </button>
              </div>
              <div className="col-sm-7">
                <a href="#/" className="forgot-login">
                  Forgot login details?
                </a>
              </div>
            </div>
          </form>
          <div className="copy">&copy; 2019 SYNECHRON. ALL RIGHTS RESERVED</div>
        </div>
      </div>
    );
  }
}
export default Login;
